<template>
  <div class="confirmOrder">
    <div class="confirmOrder_main">
      <van-cell center icon="location" is-link label="上海市闵行区上海康城小区城道80号503室">
        <template #title>
          <span class="custom-title">张杰医师</span>
          <span class="phone">127400274930</span>
        </template>
      </van-cell>
      <div class="cell_info">
        <div class="info_first">
          <div class="info_image">
            <van-image width="100%" height="100%" src="https://img.yzcdn.cn/vant/cat.jpeg" />
          </div>
          <div>
            <div>
              <div>宁夏枸杞</div>
              <div>
                <div>
                  <span>
                    250/g 一袋
                    <van-icon name="play" />
                  </span>
                </div>
              </div>
            </div>
            <div>
              <div>¥19.9</div>
              <div>×1</div>
            </div>
          </div>
        </div>
        <!-- <div class="button_t">
          <span @click="goPath('/refundAfter/applyRefund')">退款</span>
        </div>-->
        <div>
          <span>配送方式</span>
          <span>快递免邮</span>
        </div>
        <div>
          <span>商品优惠</span>
          <span style="color:#666666">选择输入优惠码</span>
        </div>
        <div>
          <span>共1件</span>
          <span>
            小计:
            <span>¥19.9</span>
          </span>
        </div>
      </div>
    </div>
    <div class="footer_div">
      <div>
        <div class="left">
          <span>共{{list.length}}件</span>
          <span>合计：</span>
          <!-- <span>¥{{sum | toFixed}}</span> -->
          <span class="sum">¥19.9</span>
        </div>
        <div class="right">
          <span @click="show=true">确认订单</span>
        </div>
      </div>
    </div>
    <van-overlay :show="show" @click="show=false" :safe-area-inset-bottom="true">
      <div class="wrapper">
        <div class="block" @click.stop>
          <div class="title-wrapper">
            选择支付方式
            <span class="right-icon" @click="show=false">
              <van-icon class-prefix="iconfont iconcha" name="iconcha" />
            </span>
          </div>
          <div class="content-wrapper">
            <div class="goods-sum">
              <div class="title">商品购买</div>
              <div class="sum">¥19.90</div>
            </div>
          </div>
          <div class="pay-wrapper">
            <div class="wechat" @click="goPath('pay')">
              <van-icon class-prefix="iconfont iconweixinzhifu" name="iconweixinzhifu" />
              <span>仅支持微信支付</span>
            </div>
          </div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      active: 0,
      value: "",
      list: [
        {
          title: "宁夏枸杞",
          tag: "250/g 一袋",
          price: 19.9,
          value: 1,
        },
      ],
    };
  },
  methods: {
    goPath(name) {
      this.$router.push({name});
    },
    
  },
};
</script>
<style lang="scss" scoped>
.confirmOrder {
  width: 100%;
  height: 100%;
  .confirmOrder_main {
    .van-cell {
      margin: 0 0 15px;
      padding: 10px 12px;
      i:first-child {
        width: 25px;
        height: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: #c8a060;
        color: #fff;
        font-size: 12px;
      }
      .van-cell__left-icon {
        margin-right: 12px;
      }
      .van-cell__title {
        .custom-title {
          font-size: 15px;
          color: #000000;
          margin-right: 10px;
        }
        .phone {
          font-size: 12px;
          color: #666666;
        }
        .van-cell__label {
          font-size: 13px;
          color: #000000;
        }
        .orders_id {
          font-size: 12px;
          color: #000000;
        }
      }
    }
    .cell_info {
      // padding: 5px 15px 15px;
      width: 100%;
      background-color: #fff;
      > .info_first {
        width: 100%;
        height: 95px;
        display: flex;
        padding: 15px;
        border-bottom: 1px solid #f5f5f5;
        .info_image {
          width: 90px;
          height: 100%;
          border-radius: 5px;
          overflow: hidden;
        }
        > div:last-child {
          flex-grow: 1;
          padding-left: 15px;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: baseline;
          > div:first-child {
            height: 100%;
            color: #000;
            > div:first-child {
              font-size: 16px;
            }
            > div:last-child {
              font-size: 13px;
              margin-top: 18px;
              > div {
                display: inline-flex;
                align-items: center;
                position: relative;
                background-color: RGBA(210, 210, 210, 0.6);
                border-radius: 5px;
                padding: 3px 10px;
                span {
                  display: inline-block;
                  max-width: 140px;
                  white-space: nowrap;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  padding-right: 15px;
                  color: #333333;
                }
                i {
                  position: absolute;
                  right: 7px;
                  top: 6px;
                  transform: rotate(90deg);
                  color: #454545;
                }
              }
            }
          }
          > div:last-child {
            height: 100%;
            text-align: right;
            > div:first-child {
              font-size: 13px;
              color: #000;
            }
            > div:last-child {
              font-size: 13px;
              margin-top: 2px;
              color: #666666;
            }
          }
        }
      }
      > div {
        display: flex;
        justify-content: space-between;
        padding: 15px 15px 0;
        font-size: 14px;
      }
      > div:last-child {
        margin-top: 25px;
        font-size: 14px;
        padding: 15px 15px;
        justify-content: flex-end;
        color: #666666;
        > span:last-child {
          color: #000;
          margin-left: 10px;
          > span {
            margin-left: 2px;
            color: #7e3330;
          }
        }
      }
    }
  }
  .footer_div {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: auto;
    padding: 4px 10px;
    height: 45px;
    line-height: 36px;
    background-color: #fff;
    border-top: 1px solid #d1d1d1;
    &::after {
      content: "";
      clear: both;
    }
    > div {
      display: flex;
      float: right;
      color: #000;
      height: 100%;
      > div {
        height: 100%;
      }
      .left {
        display: flex;
        line-height: 36px;
        span:first-child {
          margin-right: 15px;
        }
        .sum {
          font-size: 18px;
          color: #7e3330;
          font-weight: bold;
          margin-right: 10px;
        }
      }
      .right {
        span:last-child {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          background-image: linear-gradient(90deg, #e6c469 0%, #c8a060 100%),
            linear-gradient(#7e3330, #7e3330);
          background-blend-mode: normal, normal;
          border-radius: 35px;
          color: #fff;
          width: 120px;
          height: 100%;
        }
      }
    }
  }
  .goods-sum {
    padding: 10px 0 30px;
    line-height: 35px;
    background-color: #f5f5f5;
    text-align: center;
    .title {
      color: #333333;
    }
    .sum {
      font-size: 35px;
      color: #000;
    }
  }
}
</style>



